.page-title{
    text-align: center;
    margin-bottom: 3rem;
    font-size: 3.5rem;
}

.page-subtitle{
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
}

.page-caption{
    text-align: center;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.08rem;
}

/* Card Component related */
.content-card{
    max-width: 16rem;
    margin: 1rem;
}

.content-container{
    max-width: 80%;
    padding: 0 0 0 2.5rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: start;
}

.card-text-container{
    height: 5rem;
}

.content-card-image{
    height: 0;
    padding-top: 50%;
}